<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "extensionCensor-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      ins4CensorTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "create_Date", label: "申請日期", width: "125" },
        { id: "personInfo", label: "姓名(性別) 身分證字號", width: "175" },
        { id: "s_Id", label: "廠商統編", width: "125" },
        { id: "insurance_Begin", label: "保險展延期間(起)", width: "150" },
        { id: "insurance_End", label: "保險展延期間(迄)", width: "150" },
        { id: "file_Name", label: "附檔", width: "" },
      ],

      approveVisible: false, // 同意視窗
      approveItem: [],
      rejectVisible: false, // 退回視窗
      rejectItem: [],
      rejectReason: "", // 退件說明
    };
  },
  computed: {
    // 待審核保險展延資料 (All)
    ins4Censor() {
      return this.$store.state.ins_forcensor;
    },
    // 待審核保險展延資料 (單頁)
    ins4CensorList() {
      return this.$store.state.ins_forcensorlist;
    },
  },
  methods: {
    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      console.log(item);
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.file_Id,
          router_Name: config.getInsuranceArea,
        });
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${item.file_Name}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },

    // 開啟視窗，並代入相應的資訊
    approveDialog(item) {
      this.approveVisible = true;
      this.approveItem = [item];
    },
    rejectDialog(item) {
      this.rejectVisible = true;
      this.rejectItem = [item];
    },
    handleClose(done) {
      this.$confirm("確認關閉？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },

    async approveCensor() {
      this.approveItem[0].rn = "";
      try {
        const res = await this.$store.dispatch("approveIns", this.approveItem);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "審核成功",
          });

          // 關閉視窗，並更新待審核清單 (呈現第一頁)
          this.approveVisible = false;
          this.$store.commit("CURRENT_PAGE_INIT");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async rejectCensor() {
      if (!this.rejectReason) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請提供退件原因",
        });
      } else {
        this.rejectItem[0].rn = "";
        this.rejectItem[0].reject_Reason = this.rejectReason;
        try {
          const res = await this.$store.dispatch("rejectIns", this.rejectItem);
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "審核成功",
            });

            // 清空表單、關閉視窗，並更新待審核清單 (呈現第一頁)
            this.rejectVisible = false;
            this.rejectReason = "";
            this.$store.commit("CURRENT_PAGE_INIT");
          }
        } catch (err) {
          console.log(err);
        }
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "ins_forcensor",
        paramsListName: "ins_forcensorlist",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    try {
      await this.$store.dispatch("getInsForCensor");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./extensionCensor.html"></template>
